import { Component, OnInit } from '@angular/core';
import { LoginService } from '../services/login.service';
import { Router } from '@angular/router';
import {
  AuthService,
  FacebookLoginProvider,
  GoogleLoginProvider
} from 'angular-6-social-login';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  constructor(private loginService: LoginService,
              private router: Router, private socialAuthService: AuthService) {
  }

  user = {
    email: '',
    password: ''
  };
  errorMessage: any;

  ngOnInit() {
  }

  /**
   * login function
   * @details: used to login the user
   */
  login() {
    this.loginService.userLogin(this.user).subscribe(
      (response: any) => {
        this.userLoginHandleResponse(response);
      },
      (error: any) => {
        this.errorMessage = 'Please check your email/password combination';
        console.log(error);
      }
    );
  }

  socialLogin(data) {
    this.loginService.userSocialLogin(data).subscribe(
      (response: any) => {
        this.userLoginHandleResponse(response);
      },
      (error: any) => {
        this.errorMessage = error.message;
        console.log(error);
      }
    );
  }

  // To handle the user login response.
  userLoginHandleResponse(response) {
    localStorage.setItem('_u', JSON.stringify(response));
    this.router.navigate(['home']);
  }

  public socialSignIn(socialPlatform: string) {
    let socialPlatformProvider;
    if (socialPlatform === 'facebook') {
      socialPlatformProvider = FacebookLoginProvider.PROVIDER_ID;
    }

    this.socialAuthService.signIn(socialPlatformProvider).then(
      (userData) => {
        console.log(socialPlatform + ' sign in data : ' , userData);
        this.socialLogin({
          name: userData.name,
          email: userData.email,
          password: '',
          socialID: userData.id
        });
      }
    ).catch((error) => {
      this.errorMessage = error.message;
    });
  }
}
