import { Injectable } from '@angular/core';
import {Observable, Observer, Subject} from 'rxjs';
import { GlobalUrls } from '../utils/global-urls';
import { ApiCallService } from './api.service';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  constructor(private apiCall: ApiCallService) {}
  /**
   * user login function
   * @ type: POST
   */
  userLogin(data: any): Observable<any> {
    return Observable.create((observer: Observer<any>) => {
      this.apiCall.callPostApi(`${GlobalUrls.LOGIN_URL}`, data, {}).subscribe((response) => {
        if (response) {
          observer.next(response);
        } else {
          observer.error('error in getting details');
        }
      }, (error) => {
        observer.error(error);
      });
    });
  }

  userSocialLogin(data: any): Observable<any> {
    return Observable.create((observer: Observer<any>) => {
      this.apiCall.callPostApi(`${GlobalUrls.LOGIN_URL}`, data, {}).subscribe((response) => {
        if (response) {
          observer.next(response);
        } else {
          observer.error('error in getting details');
        }
      }, (error) => {
        observer.error(error);
      });
    });
  }
}
